.land_contact_but
{
    display: inline;
    margin-bottom: 30px;
    margin-top: 10px;
}
.land_card{ 
width: 1100px;
background: #FFFFFF;
border-radius: 35px;
margin-top: 30px;
padding-bottom: 5px;
}
.land_detail{
    width: 1100px;
    margin-top: 20px;
    justify-content: space-around;
}
.land_detail1{
    border-radius: 15px;
    width:70%;
    background-color: #F8F8F8;
    box-shadow: 4px 4px 10px 2px rgba(0, 0, 0, 0.25);
    text-align: center;
    margin-top: 10px;
    margin-bottom: 10px;
}
.land_abt_school{
    background: #F8F8F8;
    box-shadow: 4px 4px 10px 2px rgba(0, 0, 0, 0.25);
    border-radius: 35px;
    padding-left: 50px;
    padding-right: 20px;
    padding-top: 20px;
    padding-bottom: 30px;
    margin-top: 25px;
    margin-bottom: 25px;
}
.land_abt_school1{
    background: #F8F8F8;
    box-shadow: 4px 4px 10px 2px rgba(0, 0, 0, 0.25);
    border-radius: 35px;
    padding-top: 20px;
    padding-bottom: 30px;
    margin-top: 25px;
    margin-bottom: 25px;
}
.land_pagin{
    background-color:#8C30F5;
    color:#ffffff;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 50px;
    font-size: 18px;
    font-weight: 500;
}
.land_tour{
    height: 220px;
    width: 220px;
    margin-top: 20px;
}
.land_back_img
{
    width: 100%;
    border-top-left-radius: 35px;
    border-top-right-radius: 35px;
    height: 300px;
}
.land_logo
{
    height:140px;
    margin-top: -70px;
    background-color: #FFFFFF;
    margin-left: 80px;
    border-radius: 1000px;
}
.land_title{
    font-size: 35px;
    font-weight: 700;
    margin-left: 20px;
}
.land_content{
    font-size: 25px;
    font-weight: normal;
    margin-left: 20px;
}
.land_nav1
{
    font-size: 25px;
    padding-top: 5px;
    padding-bottom: 5px;
    font-weight: 500;
    border-bottom:5px solid #8C30F5 !important;
    width: fit-content;
    text-align: center !important;
    margin-left: 57px !important;
}
.land_nav
{
    font-size: 25px;
    padding-top: 5px;
    padding-bottom: 5px;
    font-weight: 500;
    width: fit-content;
    text-align: center !important;
    margin-left: 57px !important;
}
.img_principal{
    display: inline;
    float: left;
    height: 250px;
    width: 250px;
    margin-left: -20px;
    margin-top: 20px;
    margin-right: 20px;
    margin-bottom: 20px;
}
.school_info_table1_title{
    background-color: #F1E4FF;
    font-size: 21px;
    font-weight: 400;
    width: 95%;
    margin-left: 27px;
    text-align: center;
    line-height: 48px;
    display: flex;
    align-items: center;
}
.school_info_table1_content{
    font-size: 18px;
    font-weight: 300;
    width: 95%;
    margin-left: 25px;
    text-align: center;
    line-height: 44px;
    display: flex;
    align-items: center;
}
.school_info_table2_title{
    background-color: #F1E4FF;
    font-size: 21px;
    font-weight: 400;
    width: 95%;
    margin-left: 27px;
    text-align: center;
    line-height: 48px;
    display: flex;
    align-items: center;
}
.school_info_table2_content{
    font-size: 18px;
    font-weight: 300;
    width: 95%;
    margin-left: 25px;
    text-align: center;
    line-height: 44px;
    display: flex;
    align-items: center;
}
.box_title{
    padding-left: 50px;
    margin-bottom: 15px;
}
.admission_button1{
    border: 1px solid #AB68F8;
    box-sizing: border-box;
}
.admission_button2{
    color: white;
    background-color: #1F5CA2;
    border: 1px solid #1F5CA2;
    box-sizing: border-box;
    margin-left: 5px;
}
.admission_button2:hover{
    background-color:royalblue;
    border: 1px solid royalblue;
}
